  display: flex;
  padding-left: 0;
  list-style: none;
}
.pagination ul li a {
  position: relative;
  display: block;
  padding: 0.5rem 0.75rem;
  margin-left: -1px;
  line-height: 1.25;
  background-color: #ffffff;
  border: 1px solid #f4e6ff;
  text-decoration: none;
}
.pagination ul li a:hover {
  color: #9100ff;
  background-color: #f4e6ff;
  border-color: #f4e6ff;
  text-decoration: none;
}
.pagination ul li.active a {
  color: #ffffff;
  background-color: #9100ff;
  border-color: #9100ff;
  text-decoration: none;
}
.pagination ul li a:hover:not(li.active) {
  color: #9100ff;
  background-color: #f4e6ff;
  border-color: #f4e6ff;
  text-decoration: none;
}
.pagination ul li.disabled a {
  color: #6600b3;
  pointer-events: none;
  cursor: auto;
  border-color: #f4e6ff;
}

/*# sourceMappingURL=Pagination.svelte.css.map */</style>

<!-- <div class="text-center">
  <p>
    Page {current_page} / {last_page}
    <br />
